<template>
  <el-row>
  <el-row>
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/" style="font-size: 20px">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>

        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>
  </el-row>
    <el-row>
      <el-col :span="6" >
        
        <el-date-picker
          v-model="value1"
          align="right"
          type="date"
          placeholder=""
         >
        </el-date-picker>
        </el-col>
      <el-col :span="4">
        <el-button-group>
          <el-button type="primary" icon="el-icon-arrow-left" @click="lastday()"></el-button>
          <el-button type="primary"><i class="el-icon-arrow-right el-icon--right" @click="nextday"></i></el-button>
        </el-button-group>
      </el-col>
      <el-col :span="6">

      </el-col>
    </el-row>
    <el-row >
      <el-col :span="4">

      </el-col>
      <el-col :span="6" class="zongti">

        <el-table
          size="mini"
          ref="singleTable"
          :data="tableData"
          highlight-current-row
          @current-change="handleCurrentChange"
          style="width: 100%">
          <el-table-column

            :label="usenum"
            width="120">
            <template slot-scope="scope" >
              <a href="#">{{scope.row.vehdif}}</a>
              <el-badge :value="scope.row.keyong" type="warning" />


            </template>
          </el-table-column>
          <el-table-column

            :label="appnum"
            width="120">
            <template slot-scope="scope" >
              <a href="#">{{scope.row.vehdif}}</a>
              <el-badge :value="scope.row.yianpai" type="danger" />


            </template>
          </el-table-column>
          <el-table-column

            :label="limitnum"
            width="120">
            <template slot-scope="scope" >
              <a href="#">{{scope.row.vehdif}}</a>
              <el-badge :value="scope.row.xianxing" type="info" />


            </template>
          </el-table-column>

        </el-table>

      </el-col>
      <el-col :span="4" class="app-btn">
        <i class="el-icon-success " ></i><br>
        <i class="el-icon-info"></i><br>
        <i class="el-icon-error"></i><br><br>
        <el-button type="success" @click="apply()"></el-button>
        <el-dialog
          title=""
          :visible.sync="dialogVisible"
          width="50%"
          center>
          <el-form :model="ruleForm1" :rules="rules1" status-icon inline  ref="ruleForm1" label-width="140px" size="small" class="demo-ruleForm">
            <el-form-item label="" prop="yccs">
              <el-input v-model="ruleForm1.yccs"></el-input>
            </el-form-item>
            <el-form-item label="" prop="ycr">
              <el-input v-model="ruleForm1.ycr"></el-input>
            </el-form-item>
            <el-form-item label="" prop="cssj">
              <el-input v-model="ruleForm1.cssj"></el-input>
            </el-form-item>
            <el-form-item label="" prop="czrs">
              <el-input v-model="ruleForm1.czrs"></el-input>
            </el-form-item>
            <el-form-item label="" prop="mdd">
              <el-input v-model="ruleForm1.mdd"></el-input>
            </el-form-item>
            <br>
            <el-form-item label=""  prop="ycsj">
              <el-col :span="21">
                <el-date-picker type="datetime" placeholder="" v-model="ruleForm1.ycsj" style="width: 100%;"></el-date-picker>
              </el-col>
            </el-form-item>
            <el-form-item label=""  prop="nfhsj">
              <el-col :span="21">
                <el-date-picker type="datetime" placeholder="" v-model="ruleForm1.nfhsj" style="width: 100%;"></el-date-picker>
              </el-col>
            </el-form-item>
            <br>
            <el-form-item label="" prop="isnight">
              <el-radio-group v-model="ruleForm1.isnight">
                <el-radio label="" name="0"></el-radio>
                <el-radio label="" name="1"></el-radio>
              </el-radio-group>
            </el-form-item>
            <br>
            <el-form-item label="" prop="sfwfyc">
              <el-radio-group v-model="ruleForm1.sfwfyc">
                <el-radio label="" name="0"></el-radio>
                <el-radio label="" name="1"></el-radio>
              </el-radio-group>
            </el-form-item>
            <br>
            <el-form-item label="" prop="sfxysj">
              <el-radio-group v-model="ruleForm1.sfxysj">
                <el-radio label="" name="0"></el-radio>
                <el-radio label="" name="1"></el-radio>
              </el-radio-group>
            </el-form-item>
            <br>
            <el-form-item label="" prop="ycsy">
              <el-input type="textarea" v-model="ruleForm1.ycsy"></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
               <el-button @click="dialogVisible = false;resetForm1('ruleForm1')"> </el-button>
               <el-button type="success" @click="resetForm1('ruleForm1')"></el-button>
               <el-button type="primary" @click="submitForm1('ruleForm1')"> </el-button>
        </span>
        </el-dialog>

      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-table
          :data="managertable"
          border
          style="width: 100%">
          <el-table-column
            prop="apstatus"
            label=""
            width="40">
            <template slot-scope="scope">
              <i class="el-icon-success " v-if="scope.row.apstatus=='1'"></i>
              <i class="el-icon-info" v-else-if="scope.row.apstatus=='0'"></i>
              <i class="el-icon-error" v-else></i>
            </template>
          </el-table-column>
          <el-table-column
            prop="ycsj"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="ycr"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="ycsy"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="ycrs"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="mdd"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="sfxysj"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="csspr"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="apqk"
            label=""
          >
          </el-table-column>
          <el-table-column
            prop="sqsj"
            label=""
            >
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24" style="margin-left: 100px">
        <el-pagination
          background
          layout="prev, pager, next"
          :page-count="pagecount"
          @current-change="handleChangepage">
        </el-pagination>
      </el-col>
    </el-row>
  </el-row>
</template>

<script>
    export default {
        name: "Useinfo",
      data(){
          return{
            ruleForm1: {
              yccs: '',
              ycr: '',
              cssj: '',
              czrs: '',
              mdd: '',
              ycsj:'',
              nfhsj:'',
              isnight:'',
              sfwfyc:'',
              sfxysj:'',
              ycsy:'',
            },
            rules1: {
              yccs: [
                { required: true, message: '', trigger: 'blur' },

              ],
              ycr: [
                { required: true, message: '', trigger: 'change' }
              ],
              cssj: [
                { required: true, message: '', trigger: 'change' }
              ],
              ycsj: [
                { required: true, message: '', trigger: 'change' }
              ],
              nfhsj: [
                { required: true, message: '', trigger: 'change' }
              ],
              mdd: [
                { required: true, message: '', trigger: 'change' }
              ],

              isnight: [
                { required: true, message: '', trigger: 'change' }
              ],
              sfwfyc: [
                { required: true, message: '', trigger: 'change' }
              ],
              sfxysj: [
                { required: true, message: '', trigger: 'change' }
              ],
              ycsy: [
                { required: true, message: '', trigger: 'change' }
              ],



            },
            managertable: [],
            pickerOptions1: {
              disabledDate(time) {

              },

            },
            value1:'',
            tableData: [{
              vehdif:'',
              keyong: this.enablexiaonum,
              yianpai: this.arrdanum,
              xianxing: this.limitxiaonum,
            }, {
              vehdif:'',
              keyong: this.enablezhongnum,
              yianpai: this.arrzhongnum,
              xianxing: this.limitzhongnum,
            }, {
              vehdif:'',
              keyong: this.enabledanum,
              yianpai: this.arrdanum,
              xianxing: this.limitdanum,
            }, ],
            usenum:'',
            appnum:'',
            limitnum:'',
            dialogVisible:false,

            pagecount:0,
            currentpage:1,
            currentvehnum:'',

          }
    },
    mounted:function () {
      this.applyTableInfo();
      this.handlePageSearch(1,10);
    },
    methods:{
      handleChangepage(val) {
        console.log(`: ${val}`);
        this.currentpage = val;
        console.log("dangqianye",this.currentpage);
        this.handlePageSearch(this.currentpage,10);
      },
      applyTableInfo(){
        this.$ajax.get('/getVehNum').then((res)=>{
          if (res.data.status) {
            console.log(res.data);
            this.tableData[0].keyong = res.data.enablexiaonum;
            this.tableData[1].keyong = res.data.enablezhongnum;
            this.tableData[2].keyong = res.data.enabledanum;
            this.tableData[0].yianpai = res.data.arrxiaonum;
            this.tableData[1].yianpai = res.data.arrzhongnum;
            this.tableData[2].yianpai = res.data.arrdanum;
            this.tableData[0].xianxing = res.data.limitxiaonum;
            this.tableData[1].xianxing = res.data.limitzhongnum;
            this.tableData[2].xianxing = res.data.limitdanum;
          } else {
            console.log(res.data.status)
            this.$message({
              type: 'error',
              message: '',
              showClose: true
            })
          }
        }).catch((err) => {
          this.$message({
            type: 'error',
            message: '',
            showClose: true
          })
        })

      },
      handlePageSearch(currentpage,limit){
        let param = new URLSearchParams();
        param.append("currentpage", currentpage);
        param.append("limit", limit);
        this.$ajax.post('/ycsqInfo',param).then((res)=>{
          if (res.data.status) {
            console.log(res.data),
              this.managertable = res.data.ycsqlist;
            this.pagecount = res.data.pagecount;
            console.log("this.pagecount"+this.pagecount)

          } else {
            console.log(res.data.status)
            this.$message({
              type: 'error',
              message: '',
              showClose: true
            })
          }
        }).catch((err) => {
          this.$message({
            type: 'error',
            message: '',
            showClose: true
          })
        })
      },
      handleChangepage(val) {
        console.log(`: ${val}`);
        this.currentpage = val;
        console.log("dangqianye",this.currentpage);
        this.handlePageSearch(this.currentpage,10);
      },
      apply(){
            this.dialogVisible=true;
      },
      lastday(){
        this.value1 = new Date(this.value1.getTime()-3600*1000*24);
      },
      nextday(){
        this.value1 = new Date(this.value1.getTime()+3600*1000*24);
      },
      resetForm1(formName) {
        this.$refs[formName].resetFields();
      },
      submitForm1(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid){
            console.log(JSON.stringify(this.ruleForm1));
            let param = new URLSearchParams();
            param.append("ruleForm1", JSON.stringify(this.ruleForm1));
            this.$ajax.post('/addYcsq',param).then((res)=>{
              if (res.data.status) {
                console.log(res.data);

              } else {
                console.log(res.data.status)
                this.$message({
                  type: 'error',
                  message: '',
                  showClose: true
                })
              }
            }).catch((err) => {
              this.$message({
                type: 'error',
                message: '',
                showClose: true
              })
            })
            this.$message({
              type: 'success',
              message: '',
              showClose: true
            })
            this.dialogVisible = false;
          }else {
            console.log('error submit!!');
            return false;
          }
        });
      },
    }
    }
</script>

<style scoped>

  .app-btn{
    margin-top: 40px;
  }
  .zongti{

    margin: 10px;
    height: auto;

  }
</style>



