<script>
    export default {
      name: "Veninfo",
      data(){
        return{
          ruleForm: {
            vehnum: '',
            vehversion: '',
            seatsnum: '',
            color: '',
            enginenum: '',
            gasvol:'',
            vehdif:'',
            vehusage:'',
            policynum:'',
            framenum:'',
            oilcardnum:'',
            oildif:'',
            purdate:'',
            supplier:'',
            weight:'',
            travel:'',
            isscrap:false,
            remarks:'',

          },
          ruleForm1: {
            vehnum: '',
            vehversion: '',
            seatsnum: '',
            color: '',
            enginenum: '',
            gasvol:'',
            vehdif:'',
            vehusage:'',
            policynum:'',
            framenum:'',
            oilcardnum:'',
            oildif:'',
            purdate:'',
            supplier:'',
            weight:'',
            travel:'',
            isscrap:false,
            remarks:'',

          },
          rules: {
            vehnum: [
              { required: true, message: '', trigger: 'blur' },
              { min: 1, max: 8, message: ' 3  5 ', trigger: 'blur' }
            ],
            vehversion: [
              { required: true, message: '', trigger: 'change' }
            ],
            seatsnum: [
              { required: true, message: '', trigger: 'change' }
            ],
            color: [
              { required: true, message: '', trigger: 'change' }
            ],
            enginenum: [
              { required: true, message: '', trigger: 'change' }
            ],
            gasvol: [
              { required: true, message: '', trigger: 'change' }
            ],
            vehdif: [
              { required: true, message: '', trigger: 'change' }
            ],
            vehusage: [
              { required: true, message: '', trigger: 'change' }
            ],
            policynum: [
              { required: true, message: '', trigger: 'change' }
            ],
            framenum: [
              { required: true, message: '', trigger: 'change' }
            ],
            oilcardnum: [
              { required: true, message: '', trigger: 'change' }
            ],
            oildif: [
              { required: true, message: '', trigger: 'change' }
            ],

            purdate: [
              { type: 'date', required: true, message: '', trigger: 'change' }
            ],


          },
          rules1: {
            vehnum: [
              { required: true, message: '', trigger: 'blur' },
              { min: 3, max: 6, message: ' 3  5 ', trigger: 'blur' }
            ],
            vehversion: [
              { required: true, message: '', trigger: 'change' }
            ],
            seatsnum: [
              { required: true, message: '', trigger: 'change' }
            ],
            color: [
              { required: true, message: '', trigger: 'change' }
            ],
            enginenum: [
              { required: true, message: '', trigger: 'change' }
            ],
            gasvol: [
              { required: true, message: '', trigger: 'change' }
            ],
            vehdif: [
              { required: true, message: '', trigger: 'change' }
            ],
            vehusage: [
              { required: true, message: '', trigger: 'change' }
            ],
            policynum: [
              { required: true, message: '', trigger: 'change' }
            ],
            framenum: [
              { required: true, message: '', trigger: 'change' }
            ],
            oilcardnum: [
              { required: true, message: '', trigger: 'change' }
            ],
            oildif: [
              { required: true, message: '', trigger: 'change' }
            ],

            purdate: [
              { type: 'date', required: true, message: '', trigger: 'change' }
            ],


          },
          centerDialogVisible: false,
          centerDialogVisible1:false,
          tableData: [],
          currentRow:null,
          vehlist:'',
          state4:'',
          state5:'',
          pagecount:0,
          currentpage:1,
          currentvehnum:'',

        }


      },
      mounted:function () {

       this.handlePageSearch(1,10);

        },
      methods:{
        searchbao(vehnum){
          this.$router.push({path:'/baoinfo',query:{vehnum:vehnum}});
          console.log("vehnumzhiqian"+vehnum);
        },
        searchrep(vehnum){
          this.$router.push({path:'/repinfo',query:{vehnum:vehnum}});
          console.log("vehnumzhiqian"+vehnum);
        },
        searchshi(vehnum){
          this.$router.push({path:'/shiinfo',query:{vehnum:vehnum}});
          console.log("vehnumzhiqian"+vehnum);
        },
        searchwei(vehnum){
          this.$router.push({path:'/weiinfo',query:{vehnum:vehnum}});
          console.log("vehnumzhiqian"+vehnum);
        },
        handlePageSearch(currentpage,limit){
          let param = new URLSearchParams();
          param.append("currentpage", currentpage);
          param.append("limit", limit);
          this.$ajax.post('/vehInfo',param).then((res)=>{
            if (res.data.status) {
              console.log(res.data),
                this.tableData = res.data.vehlist;
              this.pagecount = res.data.pagecount;
              console.log("this.pagecount"+this.pagecount)

            } else {
              console.log(res.data.status)
              this.$message({
                type: 'error',
                message: '',
                showClose: true
              })
            }
          }).catch((err) => {
            this.$message({
              type: 'error',
              message: '',
              showClose: true
            })
          })
        },
        handleChangepage(val) {
          console.log(`: ${val}`);
          this.currentpage = val;
          console.log("dangqianye",this.currentpage);
          this.handlePageSearch(this.currentpage,10);
        },
        handleCurrentChange(val) {
          this.currentRow = val;
        },
        handleEdit(index, row) {
          //console.log(index, row);
          console.log(row.vehnum);
          this.ruleForm.vehnum = row.vehnum;
        },
        handleDelete(index, row) {
          console.log(index, row);
          this.currentvehnum = row.vehnum;
        },
        open2() {
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            let param = new URLSearchParams();
            param.append("vehnum", this.currentvehnum);
            this.$ajax.post('/deleteVeh',param).then((res)=>{
              if (res.data.status) {
                console.log(res.data);
                this.$message({
                  type: 'success',
                  message: '!'
                });

              } else {
                console.log(res.data.status)
                this.$message({
                  type: 'error',
                  message: '',
                  showClose: true
                })
              }
            }).catch((err) => {
              this.$message({
                type: 'error',
                message: '',
                showClose: true
              })
            })

          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        },
        open1(){
          this.centerDialogVisible = true;

        },
        open3(){
          this.centerDialogVisible1 = true;
        },
        resetForm(formName) {
          this.$refs[formName].resetFields();
        },
        submitForm(formName) {
          this.$refs[formName].validate((valid) => {
            if (valid) {
              console.log(JSON.stringify(this.ruleForm));
              let param = new URLSearchParams();
              param.append("ruleForm", JSON.stringify(this.ruleForm));
              this.$ajax.post('/updateVeh',param).then((res)=>{
                if (res.data.status) {
                  console.log(res.data);

                } else {
                  console.log(res.data.status)
                  this.$message({
                    type: 'error',
                    message: '',
                    showClose: true
                  })
                }
              }).catch((err) => {
                this.$message({
                  type: 'error',
                  message: '',
                  showClose: true
                })
              })
              this.$message({
                type: 'success',
                message: '',
                showClose: true
              })
              this.centerDialogVisible = false;
            } else {
              console.log('error submit!!');
              return false;
            }
          });

        },
        resetForm1(formName) {
          this.$refs[formName].resetFields();
        },
        submitForm1(formName) {
          this.$refs[formName].validate((valid) => {
            if (valid) {
              console.log(JSON.stringify(this.ruleForm1));
              let param = new URLSearchParams();
              param.append("ruleForm1", JSON.stringify(this.ruleForm1));
              this.$ajax.post('/addVeh',param).then((res)=>{
                if (res.data.status) {
                  console.log(res.data);

                } else {
                  console.log(res.data.status)
                  this.$message({
                    type: 'error',
                    message: '',
                    showClose: true
                  })
                }
              }).catch((err) => {
                this.$message({
                  type: 'error',
                  message: '',
                  showClose: true
                })
              })
              this.$message({
                type: 'success',
                message: '',
                showClose: true
              })
              this.centerDialogVisible1 = false;
            } else {
              console.log('error submit!!');
              return false;
            }
          });
        },

      }
    }


</script>

<style scoped>
.fd-option{
  margin:10px;
}
  .fd-table{
    margin: 10px;
  }
  .cell{
    height: 50px;
  }
</style>



