import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'




import Login from '@/components/Login'


import Veninfo from '@/components/info/Veninfo'


import Perinfo from '@/components/info/Perinfo'
import Baoinfo from '@/components/info/Baoinfo'
import Weiinfo from '@/components/info/Weiinfo'
import Shiinfo from '@/components/info/Shiinfo'
import Repinfo from '@/components/info/Repinfo'
import Xianinfo from '@/components/info/Xianinfo'
import Useinfo from '@/components/vehicle/Useinfo'
import Waitedapp from '@/components/vehicle/Waitedapp'
import Aprovedapp from '@/components/vehicle/Aprovedapp'
import Waitedarr from '@/components/vehicle/Waitedarr'
import Arranged from '@/components/vehicle/Arranged'
import Recordsearch from '@/components/search/Recordsearch'
import Checkvehsearch from '@/components/search/Checkvehsearch'
import Checkdriversearch from '@/components/search/Checkdriversearch'
Vue.use(Router)

let router = new Router({
// mode: 'history',
  routes: [
    {
      path:'/login',
      name:'login',
      component:Login,


    },
    {
      path: '/',
      name: '',
      component: Home,

      leaf: false,
      menuShow: true,
      iconCls: 'iconfont icon-home', // class
      children: [
        {path: '/veninfo', component: Veninfo, name: '',userrole:1, menuShow: true},
        {path: '/perinfo', component: Perinfo, name: '', menuShow: true},
        {path: '/baoinfo', component: Baoinfo, name: '', menuShow: true},
        {path: '/weiinfo', component: Weiinfo, name: '', menuShow: true},
        {path: '/shiinfo', component: Shiinfo, name: '', menuShow: true},
        {path: '/repinfo', component: Repinfo, name: '', menuShow: true},
        {path: '/xianinfo', component: Xianinfo, name: '', menuShow: true}
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',

      menuShow: true,
      leaf: false,
      iconCls: 'iconfont icon-users', // class
      children: [
        {path: '/useinfo', component:Useinfo, name: '',userrole:1, menuShow: true},
        {path: '/waitedapp', component:Waitedapp, name: '', userrole:1, menuShow: true},
        {path: '/aprovedapp', component:Aprovedapp, name: '', userrole:1, menuShow: true},

        {path: '/waitedarr', component:Waitedarr, name: '', userrole:1, menuShow: true},
        {path: '/arranged', component:Arranged, name: '',  userrole:1, menuShow: true},

      ]
    },
    {
      path: '/',
      component: Home,
      name: '',

      menuShow: true,
      leaf: false,
      iconCls: 'iconfont icon-users', // class
      children: [
        {path: '/recordsearch', component:Recordsearch , name: '', menuShow: true},
        {path: '/checkvehsearch', component:Checkvehsearch , name: '', menuShow: true},
        {path: '/checkdriversearch', component:Checkdriversearch , name: '', menuShow: true},
      ]
    },


  ]
})



export default router



